import axios from 'axios';
import { Comic } from '@/model';
import encryptionHandler from '@/cryptography/encryptionHandler';

const axiosInstance = axios.create();

class ApiService {
  public useApi = false;

  public async getComic(id: string): Promise<Comic> {

    if (this.useApi) {
      const comicResponse = await axiosInstance.get<Comic>(`/api/comic/${id}`, {
        responseType: 'json',
      });

      return await comicResponse.data;
    }

    const encryptedResponse = await axiosInstance.get<ArrayBuffer>(`/data/comic/${id}/data`, {
      responseType: 'arraybuffer',
    });

    const json = new TextDecoder().decode(await this.decrypt(encryptedResponse.data));
    return JSON.parse(json) as Comic;
  }

  public async downloadImage(url: string): Promise<string> {
    if (this.useApi) {
      return url;
    }

    const response = await axiosInstance.get<ArrayBuffer>(url, {
      responseType: 'arraybuffer',
    });

    const blob = new Blob([await this.decrypt(response.data)], {
      type: 'image/jpg',
    });

    return URL.createObjectURL(blob);
  }

  private async decrypt(data: ArrayBuffer): Promise<ArrayBuffer> {

    const key = sessionStorage.getItem("key")!.split('.');
    const passPrhase = key[0];
    const salt = key[1];
    const iv = key[2];

    const jsonBinarySetting = {
      "DerivationType":"scrypt",
      "Salt":salt,
      "Cost":16384,
      "BlockSize":8,
      "Parallel":1,
      "KeySizeInBytes":32,
      "DerivationIterations":0,
      "AesRijndaelIv":iv,
    };

    const eH = new encryptionHandler();
    const derivedKey = eH.getDerivedKey(passPrhase, jsonBinarySetting);

    return await eH.decryptBinaryWithDerivedKey(data, derivedKey, jsonBinarySetting);
  }
}

export default new ApiService();
